.projects {
  height: 100vh;
  background-color: #1e1e1e;
  &__title {
    text-transform: uppercase;
    text-align: center;
    position: relative;
    color: #fff;
    font-family: "Inter";
    padding-top: 100px;
    margin: 0;
    @media (min-width: 992px) {
      text-align: left;
      font-size: 40px;
    }
    &::after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      right: 0;
      height: 2px;
      background: linear-gradient(
        132.77deg,
        #e0d2ff 7.48%,
        #efc4fe 27%,
        #e9fffb 58.5%,
        #b7b2f2 92.67%
      );
      @media (min-width: 992px) {
        bottom: 10px;
        left: 250px;
      }
    }
  }

  &__list {
    list-style-type: none;
    border: 1px solid #fff;
    border-radius: 35px;
    margin-top: 20px;
    padding: 5px 15px;
    width: 150px;
  }

  &__images {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
}
.dropdown-content-tabs {
  display: none;
  transition: 0.3s ease;
}
.show {
  display: block;
  z-index: 0;
}
.tabs__btn {
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;

  font-family: "Inter";
  display: inline;
  border: 1px solid transparent;
  transition: 0.3s;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}
.tabs__btn:hover {
  border-bottom: 1px solid #fff;
}
.img-tabs {
  height: 250px;
  margin-top: 20px;
}
.img-tabs img {
  height: 100%;
}
.three {
  display: none;
  @media (min-width: 568px) {
    display: block;
  }
}

.tabs__content {
  display: none;
}

.tabs__content--active {
  display: unset;
  display: flex;
}
